@import url(https://fonts.googleapis.com/css?family=Courier+Prime&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	font-family: 'Courier Prime', monospace;
}

.container {
  position: absolute;
  width: 100%;
  height: 700px;
  background: rgb(2,0,36);
  background: -webkit-gradient(linear, left top, right top, from(rgba(2,0,36,1)), color-stop(35%, rgba(100,121,9,1)), to(rgba(0,212,255,1)));
  background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(100,121,9,1) 35%, rgba(0,212,255,1) 100%);
}

.Instructions {
  margin: auto;
  padding: 70px 0;
  text-align: center;
  font-weight: bold;
  font-size: 1.5em;
}

.gen-box {
	display: flex;
	justify-content: center;
	margin: auto;
	padding: 70px 0;
	
}

.gen-btn {
	padding: 40px 60px;
	font-size: 40px;
	-webkit-transition-duration: 0.4s; /* Safari */
  	transition-duration: 0.4s;
	border-radius: 12px;
}

.gen-btn:hover {
  background-color: grey;
  color: white;
}

.view {
	display: flex;
	justify-content: center;
	margin: auto;
	padding: auto 0;
}

.view p {
	font-size: 4em;
}
